import {
    createStore
} from 'vuex'
import newest from './newest.json'
import foreign from './foreign.json'
import list from './list.json'
import video from './video.json'

const store = createStore({
    state() {
        return {
            newest: newest,
            foreign: foreign,
            video: video,
            list: [...list],
            today: list,
            bill: [],
            playNow: 0,
            thisSong: '',
            playToggle: false,
            audio: null,
        }
    },
    mutations: {
        setMore(state, pData) {
            state.data.push(pData);
            state.pageNum++;
        },

        like(state, i) {
            if (state.data[i].liked == false) {
                state.data[i].likes++;
                state.data[i].liked = true;
            } else {
                state.data[i].likes--;
                state.data[i].liked = false
            }
        },

        billSort(state) {
            state.bill = state.list.sort(function (a, b) {
                return a.bill - b.bill
            })
        },

        nextTrack(state) {
            if (state.playNow < state.list.length) {
                let music = document.querySelector('audio');
                state.playNow++;
                music.pause();
                music.src = state.today[state.playNow].linkUrl;
                music.load();
                music.play();
            }
        },

        prevTrack(state) {
            if (state.playNow > 0) {
                let music = document.querySelector('audio');
                state.playNow--;
                music.pause();
                music.src = state.today[state.playNow].linkUrl;
                music.load();
                music.play();
            }
        },

        playTrack(state, i) {
            let music = document.querySelector('audio');
            state.playNow = i - 1;
            music.pause();
            music.src = state.today[state.playNow].linkUrl;
            music.load();
            music.play();
        },




    },
    actions: {
        shuffleTrack(context){
            let i = Math.floor( Math.random() * (20 - 2) ) + 1
            console.log(i);
            context.commit('playTrack', i)
        }
    }

})

export default store